import React, { useContext } from "react";
import "./ClearCartItems.scss";
import { CartItemsContext } from "../CartItemsContext/CartItemsContext";

function ClearCArtItems() {
  const { setCartItems, setTotalPrice } = useContext(CartItemsContext);
  return (
    <div className="clear-items">
      <button
        onClick={() => {
          setCartItems([]);
          setTotalPrice([]);
        }}
      >
        Clear Cart
      </button>
    </div>
  );
}

export default ClearCArtItems;
