import React, { useContext } from "react";
import "./ItemsInCart.scss";
import ItemDetails from "./ItemDetails";
import { CartItemsContext } from "../CartItemsContext/CartItemsContext";
import ClearCartItems from "../ClearCartItems/ClearCartItems";
import TotalPrice from "../TotalPrice/TotalPrice";

function ItemsInCart() {
  let setTrue;
  const { cartItems } = useContext(CartItemsContext);
  if (cartItems.length === 0) {
    setTrue = true;
  }
  return (
    <div className="purchase-list">
      {setTrue ? (
        <div className="empty-cart">
          <h2>Your Cart is empty</h2>
        </div>
      ) : (
        cartItems.map((cartItem, index) => {
          return (
            <>
              <ItemDetails
                id={index}
                key={index}
                name={cartItem.name}
                price={cartItem.mPrice}
                image={cartItem.image}
              />
            </>
          );
        })
      )}
      <div style={setTrue ? { display: "none" } : { display: "block" }}>
        <TotalPrice />
        <ClearCartItems />
      </div>
    </div>
  );
}

export default ItemsInCart;
