import React, { useState, createContext } from "react";

export const CartItemsContext = createContext();

export const CartItemsProvider = (props) => {
  const [specifications, setSpecifications] = useState({
    specs: [],
    packageContent: [],
  });
  const [previewCartItems, setPreviewCartItems] = useState({ image: "#" });
  const [cartItems, setCartItems] = useState([]);
  const [rise, setRise] = useState(1);
  const [cartShow, setCartShow] = useState(false);
  const [totalPrice, setTotalPrice] = useState([]);
  const [totalPriceIncrement, setTotalPriceIncrement] = useState(null);
  const [mobiMenu, setMobiMenu] = useState(false);
  var [showTotalPrice, setShowTotalPrice] = useState(false);
  const [deleteItemPrice, setDeleteItemPrice] = useState();

  const extra = (extraValue) => {
    setRise(extraValue);
  };

  return (
    <CartItemsContext.Provider
      value={{
        setCartItems,
        cartItems,
        setRise,
        rise,
        extra,
        specifications,
        setSpecifications,
        previewCartItems,
        setPreviewCartItems,
        cartShow,
        setCartShow,
        totalPrice,
        setTotalPrice,
        totalPriceIncrement,
        setTotalPriceIncrement,
        mobiMenu,
        setMobiMenu,
        setShowTotalPrice,
        setDeleteItemPrice,
        showTotalPrice,
        deleteItemPrice,
      }}
    >
      {props.children}
    </CartItemsContext.Provider>
  );
};
