import React, { useContext } from "react";
import "./DropDown.scss";
import ListItem from "./ListItem";
import { Link } from "react-router-dom";
import { CartItemsContext } from "../CartItemsContext/CartItemsContext";

function PhoneList() {
  const { setMobiMenu } = useContext(CartItemsContext);
  return (
    <div>
      <div className="phone-type-iphone">Mobile Phones</div>
      <ul className="iphone-list">
        <div></div>
        <Link
          to="/"
          onClick={() => {
            window.scrollTo(0, 664);
            setMobiMenu(false);
          }}
        >
          <ListItem name="iPhone" />
        </Link>

        <ListItem name="Android" />
      </ul>
    </div>
  );
}

export default PhoneList;
